


































































































































































































































































































import uniIcon from '../../components/uni-icon.vue'

export default {
	data() {
		return {
			isActive: 1,
			payList: [
				{
					name: 'zhifubaozhifu',
					value: '支付宝支付',
					icon: 'zhifubaozhifu',
					color: '#459ae5',
					size: '22',
					checked: true
				},
				{
					name: 'weixinzhifu',
					value: '微信支付',
					icon: 'weixinzhifu',
					color: '#44b549',
					size: '21'
				}
			]
		};
	},
	components: {
		uniIcon
	},
	methods: {
		levelOne: function(){
			this.isActive = 0
		},
		levelTwo: function(){
			this.isActive = 1
		},
		levelThree: function(){
			this.isActive = 2
		},
		openClick: function(){
			uni.pageScrollTo({
				scrollTop: 2000
			})
			if(this.isActive == 0){
				this.isActive = 1;				
			}	
		}
	}
}



